@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.weatherBlock{margin:20px}.wrapper{display:flex;flex-wrap:wrap}.App{margin:30px}.Todo{max-width:60%}

.clock{display:flex;width:350px;height:350px;justify-content:center;align-items:center;background:url(http://samuel-garcia.site/img/clock-sam.png);background-size:cover;border:4px solid #191919;background-color:#191919;border-radius:50%;box-shadow:-4px -4px 10px rgba(67,67,67,0.5),inset 4px 4px 10px rgba(0,0,0,0.5),inset -4px -4px 10px rgba(67,67,67,0.3),4px 4px 10px rgba(0,0,0,0.3)}.clock:before{content:"";position:absolute;width:15px;height:15px;background:#747474;border-radius:50%;z-index:999}.clock .hour,.clock .min,.clock .sec{position:absolute}.clock .hour,.hr{width:160px;height:160px}.clock .min,.mn{width:160px;height:190px}.clock .sec,.sc{width:230px;height:230px}.hr,.mn,.sc{display:flex;justify-content:center;position:absolute;border-radius:50%}.hr:before{content:"";position:absolute;width:8px;height:80px;background:#ffb510;z-index:9;border-radius:6px 6px 0 0}.mn:before{content:"";position:absolute;width:4px;height:90px;background:#8b8b8b;z-index:10;border-radius:6px 6px 0 0}.sc:before{content:"";position:absolute;width:2px;height:150px;background:#e5e5e5;z-index:11;border-radius:6px 6px 0 0}

#quote-box{align-items:center}#quote-box .author{text-align:right}#text{text-align:center;font-size:2rem;margin:0}.text-container{height:120px}.floating-button{border:none;text-decoration:none;display:inline-block;padding:7px 20px;margin:10px 20px;border-radius:30px;background-image:linear-gradient(45deg, #6ab1d7 0%, #33d9de 50%, #002878 100%);background-position:100% 0;background-size:200% 200%;font-family:'Montserrat', sans-serif;font-size:16px;font-weight:300;color:white;box-shadow:0 16px 32px 0 rgba(0,40,120,0.35);transition:.5s}.floating-button:hover{box-shadow:0 0 0 0 rgba(0,40,120,0);background-position:0 0}

.Todo{font-size:20px;letter-spacing:0.1em}.Todo .inputToDo{margin-bottom:20px}button{padding:10px;font-family:'Open Sans', sans-serif}.addBtn{color:green;font-weight:600;border:solid 2px green}input{padding:10px 11px 11px 11px;border-right:none;border-top:1px solid;border-bottom:1px solid;border-left:1px solid;font-family:'Open Sans', sans-serif}.addBtn:hover{box-shadow:1px 1px 2px 0}input:focus{outline:none}select{padding:10px;border-right:none;border-left:none;border-top:1px solid;border-bottom:1px solid;font-family:'Open Sans', sans-serif}.addBtn:focus{outline:none}.todos-container{width:40%}.todo-container{margin:10px}.filter-container{width:20%;padding:10px;border:1px solid green;border-radius:15%;text-align:left;font-family:'Open Sans', sans-serif}.category-button{display:block;border:none}.todoNothing{margin-bottom:15px;width:40%}

.input-dish{width:100px;border-color:#8a8282;margin-left:100px}

